:root {
  --main-bg-color: rgb(255, 255, 255);

  --card-border-radius: 5px;
  --main-margin: 0 10%;
  --center-max-width: 960px;

  --color-linen: #ffede1;
  --color-ivory: #f9fbf2;
  --color-light-cyan: rgb(171, 226, 236);
  --color-jordy-blue: #afcbff;
  --color-oxford-blue: rgb(27, 28, 32);

  --default-font-size: 20px; /* 18-24px */
  --header-font-size: 55px; /* 35-50px */
  --secondary-text-font-size: 16px;

  --main-text-color: black;
  --header-text-color: white;
  --secondary-text-color: rgb(115, 115, 115);

  --font-bold: "oswald_demibold";
  --font-regular: "nimbus_regular";
}

html,
body {
  height: 100%;
  min-height: 100vh;
  /* font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif; */
  font-size: var(--default-font-size);
}

body {
  overflow-y: auto;
  overflow-x: auto;
}

/* Reset button look */
button,
input[type="submit"],
input[type="reset"] {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

input[type="text"] {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  outline: inherit;
}

.root.container {
  width: 100%;
  min-width: 450px;
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 65px 1fr;
  min-height: 100vh;
  font-family: var(--font-regular);
  font-size: var(--default-font-size);
}

.header {
  position: sticky;
  top: 0;

  padding: 5px 10px;
  display: grid;
  grid-template-columns: minmax(100px, 960px);
  grid-template-rows: 1fr;
  justify-content: center;
  align-items: center;

  white-space: nowrap;

  background-color: var(--color-oxford-blue);
}

.header-items {
  color: white;

  max-width: 100%;

  display: grid;
  grid-template-columns: 1fr 1.3fr;
  grid-template-rows: 1fr;
  gap: 10px;
  justify-content: center;
  align-items: center;
}

.location-display {
  width: 100%;
  min-width: 0px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.location-search {
  justify-self: end;
  width: 100%;
}

/* Search bar and search button layout */
.location-search search form {
  display: flex;
  gap: 10px;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  border-radius: var(--card-border-radius);
}

/* Search icon and input text */
.location-search search form p {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: nowrap;

  background-color: rgb(255, 255, 255);
  padding: 3px;
  border-radius: var(--card-border-radius);
  width: 80%;
  min-width: 50px;
}

.location-search search form label {
  transform: rotateX("30");
  color: rgb(124, 124, 124);
  padding: 0 2px;
}

.header-items .location-search #location {
  border-radius: 5px;
  padding-left: 2px;
  width: 100%;
  min-width: 70px;
  color: gray;
}

.header-items .location-search button {
  background-color: rgb(211, 211, 211);
  border-radius: 5px;
  color: rgb(59, 59, 59);
  padding: 3px 10px;
}

.header-items .location-display {
  font-family: var(--font-bold);
}

.intro-search {
  margin: 20px;
  justify-self: center;
  width: 80%;
  height: 40%;
  background-color: #ffffff;
  border-radius: 5px;

  display: flex;
  justify-content: center;
  align-items: center;
}
/* Contains all main section */
.main-container {
  padding: 0 20px;
  width: 100%;
  background-color: var(--color-light-cyan);
  display: grid;
  grid-template-columns: minmax(100px, 1160px);
  grid-template-rows: 1fr;
  justify-content: center;
  align-items: start;
}

/* Contains all cards*/
.weather-cards {
  width: 100%;
  margin: 20px 0px;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem;
  font-family: var(--font-regular);
}

.left-cards,
.right-cards {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

/* Apply to all cards */
.card {
  font-family: var(--font-regular);
  max-width: 100%;
  width: 100%;
  min-width: fit-content;
  background-color: white;
  border-radius: var(--card-border-radius);
  box-shadow: 0px 2px 6px 1px rgb(92, 150, 162);
}

.card .subheader {
  padding: 18px 25px 2px;
  font-size: var(--secondary-text-font-size);
  font-family: var(--font-regular);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
  color: var(--secondary-text-color);
}

.card .subheader span:first-child {
  color: var(--secondary-text-color);
}

.card hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 2px solid #e7e7e7;
  margin: 0.2em 0;
  padding: 0;
}

.card .card-body {
  padding: 10px 20px 18px;
}

.card .icon {
  flex-shrink: 0;
}

/* Applies to the Today's Weather card */

.card.today {
  /* width: 500px; */
}

.card.today .icon {
  width: 30px;
  height: 30px;
  background-color: aquamarine;
  border-radius: 50%;
}

.card.today .temp.lo .icon {
  background-color: rgb(127, 221, 255);
}

.card.today .temp.hi .icon {
  /* background-color: rgb(255, 150, 57); */
  /* border: 2px solid yellow; */
}

.card.today .temp {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.card.today .description {
  padding-top: 10px;
}

.card.today .lo-hi-temps {
  display: flex;
  justify-content: space-between;
  font-family: var(--font-bold);
}

/* Applies to the Current Weather card */

.card.current-weather {
  /* width: 500px; */
}

.card.current-weather .icon {
  width: 80px;
  height: 80px;
  /* border-radius: 50%; */
}

.card.current-weather .card-body {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 35px;
}

.card.current-weather .card-body .card-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  gap: 10px;
}

.card.current-weather .card-body .card-left .card-left-top {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.card.current-weather .card-body .card-left .degree span {
  font-size: var(--header-font-size);
  font-family: var(--font-bold);
}

.card.current-weather .card-body .card-left .degree .f-or-c .vertical-break {
  margin: 0 2px 0 4px;
  height: calc(var(--default-font-size) - 4px);
  border: 0;
  border: 0.8px solid rgb(144, 144, 144);
  border-radius: 2px;
}

.card.current-weather #celsius {
  color: rgb(163, 163, 163);
}

.card.current-weather .card-body .card-left .current-degree-feel .degree {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  gap: 5px;
}

.card.current-weather .card-body .card-left .current-degree-feel .f-or-c {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 1px;
}

.card.current-weather .card-body .feels-like-container {
  font-size: var(--secondary-text-font-size);
}

.card.current-weather .card-body .card-right {
  width: 40%;
  text-align: right;
}

.card.current-weather .card-body .card-right > div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
  font-size: var(--default-font-size);
}

.card.current-weather .card-body .card-right > div > span:first-child {
  display: block;
  text-align: right;
  color: var(--secondary-text-color);
  margin-right: 20px;
}
.card.current-weather .card-body .card-right hr {
  width: 100%;
  display: block;
  height: 1px;
  border: 0;
  border-top: 2px solid #e7e7e7;
  margin: 0.2em 0;
  padding: 0;
  justify-self: end;
}

.card.current-weather .card-body .card-right > div > span:nth-child(2) {
  display: block;
}

/* Sun details */

.card.sun {
  /* width: 300px; */
}

.card.sun .card-body {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}

.card.sun .card-left {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 10px;
  align-items: center;
}

.card.sun .card-right {
  text-align: right;
}

.card.sun .icon {
  width: 60px;
  height: 60px;
  /* background-color: rgb(255, 195, 54); */
  border: 6px solid rgb(255, 195, 54);
  border-radius: 50%;
}

/* Moon details */

.card.moon .card-body {
  display: flex;
  flex-direction: row;
  /* flex-wrap: wrap; */
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}

.card.moon .card-body .card-left,
.card.moon .card-body .card-right {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 10px;
  align-items: center;
}

.card.moon .icon {
  width: 60px;
  height: 60px;
  background-color: rgb(159, 159, 159);
  border-radius: 50%;
}

.card.moon .icon .moon-img {
  width: 250%;
  height: 100%;
}

.card.moon .percentage {
  /* font-size: var(--header-font-size); */
}

