.location-search search form p.error {
  border: 3px solid #ff240c;
  background-color: #ffd3d3;
  color: rgb(176, 0, 0);
}

.location-search search form p.error input,
.location-search search form p.error label {
  background-color: #ffd3d3;
}

.location-search {
  position: relative;
}

#location-error {
  display: none;
}

#location-error.error {
  display: block;
}

.error-message {
  background-color: #ffd3d3;
  width: fit-content;
  min-width: fit-content;
  height: 35px;
  position: absolute;
  top: 100;
  left: 0;
  border: 3px solid rgb(255, 28, 28);
  margin-top: 3px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

#location-error.error-message:after {
  content: "Please enter a valid location";
  color: rgb(176, 0, 0);
  padding: 10px;
  text-align: center;
}

:focus {
  outline: none;
}

