IE,uDAAuD;AACzD","sourcesContent":["@font-face {\n  font-family: \"merriweather_regular\";\n  src:\n    url(\"./fonts/merriweather/merriweathersans-regular-webfont.woff2\")\n      format(\"woff2\"),\n    url(\"./fonts/merriweather/merriweathersans-regular-webfont.woff\")\n      format(\"woff\");\n  font-weight: normal;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: \"merriweather_bold\";\n  src:\n    url(\"./fonts/merriweather/merriweathersans-bold-webfont.woff2\")\n      format(\"woff2\"),\n    url(\"./fonts/merriweather/merriweathersans-bold-webfont.woff\")\n      format(\"woff\");\n  font-weight: normal;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: \"nimbus_regular\";\n  src:\n    url(\"./fonts/nimbus/nimbussanl-reg-webfont.woff2\") format(\"woff2\"),\n    url(\"./fonts/nimbus/nimbussanl-reg-webfont.woff\") format(\"woff\");\n  font-weight: normal;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: \"nimbus_bold\";\n  src:\n    url(\"./fonts/nimbus/nimbussanl-bol-webfont.woff2\") format(\"woff2\"),\n    url(\"./fonts/nimbus/nimbussanl-bol-webfont.woff\") format(\"woff\");\n  font-weight: normal;\n  font-style: normal;\n}\n\n/* 1. Use a more-intuitive box-sizing model */\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\n/* 2. Remove default margin */\n* {\n  margin: 0;\n  padding: 0;\n}\n\n/* 3. Enable keyword animations */\n@media (prefers-reduced-motion: no-preference) {\n  html {\n    interpolate-size: allow-keywords;\n  }\n}\n\nbody {\n  /* 4. Add accessible line-height */\n  line-height: 1.5;\n  /* 5. Improve text rendering */\n  -webkit-font-smoothing: antialiased;\n}\n\n/* 6. Improve media defaults */\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n  display: block;\n  max-width: 100%;\n}\n\n/* 7. Inherit fonts for form controls */\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n}\n\n/* 8. Avoid text overflows */\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  overflow-wrap: break-word;\n}\n\n/* 9. Improve line wrapping */\np {\n  text-wrap: pretty;\n}\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  text-wrap: balance;\n}\n\n/*\n  10. Create a root stacking context\n*/\n#root,\n#__next {\n  isolation: isolate;\n}\n\n@font-face {\n  font-family: \"oswald_regular\";\n  src:\n    url(\"./fonts/oswald/oswald-regular-webfont.woff2\") format(\"woff2\"),\n    url(\"./fonts/oswald/oswald-regular-webfont.woff\") format(\"woff\");\n  font-weight: normal;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: \"oswald_demibold\";\n  src:\n    url(\"./fonts/oswald/oswald-demibold-webfont.woff2\") format(\"woff2\"),\n    url(\"./fonts/oswald/oswald-demibold-webfont.woff\") format(\"woff\");\n  font-weight: normal;\n  font-style: normal;\n}\n\n:root {\n  --main-bg-color: rgb(255, 255, 255);\n\n  --card-border-radius: 5px;\n  --main-margin: 0 10%;\n  --center-max-width: 960px;\n\n  --color-linen: #ffede1;\n  --color-ivory: #f9fbf2;\n  --color-light-cyan: rgb(171, 226, 236);\n  --color-jordy-blue: #afcbff;\n  --color-oxford-blue: rgb(27, 28, 32);\n\n  --default-font-size: 20px; /* 18-24px */\n  --header-font-size: 55px; /* 35-50px */\n  --secondary-text-font-size: 16px;\n\n  --main-text-color: black;\n  --header-text-color: white;\n  --secondary-text-color: rgb(115, 115, 115);\n\n  --font-bold: \"oswald_demibold\";\n  --font-regular: \"nimbus_regular\";\n}\n\nhtml,\nbody {\n  height: 100%;\n  min-height: 100vh;\n  /* font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif; */\n  font-size: var(--default-font-size);\n}\n\nbody {\n  overflow-y: auto;\n  overflow-x: auto;\n}\n\n/* Reset button look */\nbutton,\ninput[type=\"submit\"],\ninput[type=\"reset\"] {\n  background: none;\n  color: inherit;\n  border: none;\n  padding: 0;\n  font: inherit;\n  cursor: pointer;\n  outline: inherit;\n}\n\ninput[type=\"text\"] {\n  background: none;\n  color: inherit;\n  border: none;\n  padding: 0;\n  font: inherit;\n  outline: inherit;\n}\n\n.root.container {\n  width: 100%;\n  min-width: 450px;\n  position: relative;\n  display: grid;\n  grid-template-columns: 1fr;\n  grid-template-rows: 65px 1fr;\n  min-height: 100vh;\n  font-family: var(--font-regular);\n  font-size: var(--default-font-size);\n}\n\n.header {\n  position: sticky;\n  top: 0;\n\n  padding: 5px 10px;\n  display: grid;\n  grid-template-columns: minmax(100px, 960px);\n  grid-template-rows: 1fr;\n  justify-content: center;\n  align-items: center;\n\n  white-space: nowrap;\n\n  background-color: var(--color-oxford-blue);\n}\n\n.header-items {\n  color: white;\n\n  max-width: 100%;\n\n  display: grid;\n  grid-template-columns: 1fr 1.3fr;\n  grid-template-rows: 1fr;\n  gap: 10px;\n  justify-content: center;\n  align-items: center;\n}\n\n.location-display {\n  width: 100%;\n  min-width: 0px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n.location-search {\n  justify-self: end;\n  width: 100%;\n}\n\n/* Search bar and search button layout */\n.location-search search form {\n  display: flex;\n  gap: 10px;\n  flex-direction: row;\n  justify-content: center;\n  align-items: center;\n\n  border-radius: var(--card-border-radius);\n}\n\n/* Search icon and input text */\n.location-search search form p {\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  flex-wrap: nowrap;\n\n  background-color: rgb(255, 255, 255);\n  padding: 3px;\n  border-radius: var(--card-border-radius);\n  width: 80%;\n  min-width: 50px;\n}\n\n.location-search search form label {\n  transform: rotateX(\"30\");\n  color: rgb(124, 124, 124);\n  padding: 0 2px;\n}\n\n.header-items .location-search #location {\n  border-radius: 5px;\n  padding-left: 2px;\n  width: 100%;\n  min-width: 70px;\n  color: gray;\n}\n\n.header-items .location-search button {\n  background-color: rgb(211, 211, 211);\n  border-radius: 5px;\n  color: rgb(59, 59, 59);\n  padding: 3px 10px;\n}\n\n.header-items .location-display {\n  font-family: var(--font-bold);\n}\n\n.intro-search {\n  margin: 20px;\n  justify-self: center;\n  width: 80%;\n  height: 40%;\n  background-color: #ffffff;\n  border-radius: 5px;\n\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n/* Contains all main section */\n.main-container {\n  padding: 0 20px;\n  width: 100%;\n  background-color: var(--color-light-cyan);\n  display: grid;\n  grid-template-columns: minmax(100px, 1160px);\n  grid-template-rows: 1fr;\n  justify-content: center;\n  align-items: start;\n}\n\n/* Contains all cards*/\n.weather-cards {\n  width: 100%;\n  margin: 20px 0px;\n\n  display: grid;\n  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));\n  gap: 1rem;\n  font-family: var(--font-regular);\n}\n\n.left-cards,\n.right-cards {\n  display: flex;\n  flex-direction: column;\n  gap: 1rem;\n}\n\n/* Apply to all cards */\n.card {\n  font-family: var(--font-regular);\n  max-width: 100%;\n  width: 100%;\n  min-width: fit-content;\n  background-color: white;\n  border-radius: var(--card-border-radius);\n  box-shadow: 0px 2px 6px 1px rgb(92, 150, 162);\n}\n\n.card .subheader {\n  padding: 18px 25px 2px;\n  font-size: var(--secondary-text-font-size);\n  font-family: var(--font-regular);\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: space-between;\n  gap: 8px;\n  color: var(--secondary-text-color);\n}\n\n.card .subheader span:first-child {\n  color: var(--secondary-text-color);\n}\n\n.card hr {\n  display: block;\n  height: 1px;\n  border: 0;\n  border-top: 2px solid #e7e7e7;\n  margin: 0.2em 0;\n  padding: 0;\n}\n\n.card .card-body {\n  padding: 10px 20px 18px;\n}\n\n.card .icon {\n  flex-shrink: 0;\n}\n\n/* Applies to the Today's Weather card */\n\n.card.today {\n  /* width: 500px; */\n}\n\n.card.today .icon {\n  width: 30px;\n  height: 30px;\n  background-color: aquamarine;\n  border-radius: 50%;\n}\n\n.card.today .temp.lo .icon {\n  background-color: rgb(127, 221, 255);\n}\n\n.card.today .temp.hi .icon {\n  /* background-color: rgb(255, 150, 57); */\n  /* bord